import { createStore } from 'vuex';
import axios from 'axios';

export const instance = axios.create({
  baseURL: 'https://neko-back.herokuapp.com/2.0/',
  withCredentials: true,
});

const store = createStore({
  state() {
    return {
      userData: {
        id: '',
        email: '',
        name: '',
        avatar: '',
        publicCardPacksCount: null,
        created: '',
        updated: '',
        isAdmin: false,
        verified: false,
        rememberMe: false,
        error: '',
      },
    };
  },
  getters: {
    getUserData(state) {
      return state.userData;
    },
  },
  mutations: {
    setUserData(state, payload) {
      state.userData.id = payload._id;
      state.userData.email = payload.email;
      state.userData.name = payload.name;
      state.userData.avatar = payload.avatar;
      state.userData.publicCardPacksCount = payload.publicCardPacksCount;
      state.userData.created = payload.created;
      state.userData.isAdmin = payload.isAdmin;
      state.userData.verified = payload.verified;
      state.userData.updated = payload.updated;
      state.userData.rememberMe = payload.rememberMe;
      if (payload.error) {
        state.userData.error = payload.error;
      }

      console.log(state.userData);
    },
  },
  actions: {
    async signIn(context, data) {
      const response = await fetch(
        'https://neko-back.herokuapp.com/2.0/auth/login',
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(data),
        }
      );

      const responseData = await response.json();
      console.log(responseData);

      if (!response.ok) {
        const error = new Error(responseData.message || 'Failed to sign in');
        throw error;
      }

      context.commit('setUserData', responseData);
    },
    async signOut() {
      const response = await instance.delete('auth/me');
      console.log(response.data);
      // const response = await fetch(
      //   'https://neko-back.herokuapp.com/2.0/auth/me',
      //   {
      //     method: 'DELETE',
      //     headers: {
      //       'Content-Type': 'application/json',
      //     },
      //     withCredentials: true,
      //     type: 'cors',
      //   }
      // );
      // console.log(response);
      // const responseData = await response.json();
      // console.log(responseData);
      // if (!response.ok) {
      //   const error = new Error(responseData.error || 'Failed to sign out');
      //   throw error;
      // }
    },
  },
});

export default store;
